import React from "react";
import { useState, useEffect } from "react";
import { CsvToHtmlTable } from 'react-csv-to-table';
import playoffs from './2023Playoffs.txt';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { styled } from '@mui/material/styles';

const Home = () => {
    const [data, setdata] = useState({
        Teams: []
    });

    //Pulled from MUI table documentation
    const StyledTableCell = styled(TableCell)(({ theme }) => ({
      [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
      },
      [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
      },
    }));
    
    const StyledTableRow = styled(TableRow)(({ theme }) => ({
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
      },
      // hide last border
      '&:last-child td, &:last-child th': {
        border: 0,
      },
    }));
    //Pull from text file containing playoff information
    const [text, setText] = React.useState();
    fetch(playoffs)
      .then((response) => response.text())
      .then((textContent) => {
        setText(textContent);
    });

    //From MUI Table documentation
    function createData(higher, lower, model) {
      return { higher, lower, model };
    }

    function createData2(higher, lower, model, result) {
      return { higher, lower, model, result };
    }
    //Rows to be showin in current playoff series
    const rows = [
      createData('New York Knicks', 'Miami Heat', 'Miami Heat'),
      createData('Boston Celtics', 'Philadelphia 76ers', 'Boston Celtics'),
      createData('Denver Nuggets', 'Phoenix Suns', 'Denver Nuggets'),
      createData('Golden State Warriors', 'LA Lakers', 'Golden State Warriors'),
    ];
    //rows to be shown in previous playoff series
    const prevRows = [
      createData2('Milwaukee Bucks', 'Miami Heat', 'Miami Heat', '4-1'),
      createData2('Cleveland Cavaliers', 'New York Knicks', 'New York Knicks', '4-1'),
      createData2('Philadelphia 76ers', 'Brooklyn Nets', 'Philadelphia 76ers', '4-0'),
      createData2('Boston Celtics', 'Atlanta Hawks', 'Boston Celtics', '4-2'),
      createData2('Denver Nuggets', 'Minnesota Timberwolves', 'Denver Nuggets', '4-1'),
      createData2('Phoenix Suns', 'LA Clippers', 'Phoenix Suns', '4-1'),
      createData2('Sacramento Kings', 'Golden State Warriors', 'Golden State Warriors', '4-3'),
      createData2('Memphis Grizzlies', 'LA Lakers', 'LA Lakers', '4-2'),
    ];

  //Display two tables. One showing current playoff matchups, the other showing the previous playoff matchups
  return (
    <div>
      <h1>Current Playoff Series</h1>
      <br></br>
      <div style={{ display: 'flex', justifyContent: 'center'}}>
        <div style={{ height: '275px', fontSize: '20px'}}>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 900 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <StyledTableCell align="center">Higher seed</StyledTableCell>
                <StyledTableCell align="center">Lower Seed</StyledTableCell>
                <StyledTableCell align="center">Model Prediction</StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
                <StyledTableRow
                  key={row.higher}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <StyledTableCell align="center">
                    {row.higher}
                  </StyledTableCell>
                  <StyledTableCell align="center">{row.lower}</StyledTableCell>
                  <StyledTableCell align="center">{row.model}</StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        </div>
      </div>
      <h1>Previous Playoff Series</h1>
      <br></br>
      <div style={{ display: 'flex', justifyContent: 'center'}}>
        <div style={{ height: '500px', fontSize: '20px'}}>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 900 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <StyledTableCell align="center">Higher seed</StyledTableCell>
                <StyledTableCell align="center">Lower Seed</StyledTableCell>
                <StyledTableCell align="center">Winner</StyledTableCell>
                <StyledTableCell align="center">Result</StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {prevRows.map((row) => (
                <StyledTableRow
                  key={row.higher}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <StyledTableCell align="center">
                    {row.higher}
                  </StyledTableCell>
                  <StyledTableCell align="center">{row.lower}</StyledTableCell>
                  <StyledTableCell align="center">{row.model}</StyledTableCell>
                  <StyledTableCell align="center">{row.result}</StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        </div>

      </div>
    </div>

    
  );
};
  
export default Home;