import { Typography } from "@mui/material";
import React from "react";
import models from './modelcomparison.jpg'
import neuralnet from './confusionmatrix.png'
import profit from './profit.jpg'
//Results of our project
const Results = () => {
  return (
    <div>
      <h1>Final Results</h1>
      <Typography mr={15} ml={15}>
      Our accuracy in predicting the winner of an NBA for each increased around 15-20% for each season since we changed our
      model to use 5 game rolling averages, with our neural network model topping out near 70% predicting wins and losses. 
      This change allows us to predict a winner for a game that has not been played, which takes us in the right direction 
      since eventually, we want to predict how to bet on a future game. It also exemplifies how much momentum plays a factor in todays 
      NBA. This accuracy number is on par with models discovered in our research, and we hope to increase it further as we add in the 
      capability to predict over/under betting odds. The increase in our models' performance stems from a greater focus on feature 
      extraction within our box score data set. Below is a plot of the current accuracy for wins and losses
      for each of our models, with training results in blue and testing in orange.
      </Typography>
      <br></br>
      <img src={models} alt="Model Comparison" style={{height:'300px'}}></img>
      <br></br>
      <Typography mr={15} ml={15}>
      Although, our regression and random forest models are 
      showing improvement as we continue to minimize the RMSE. Shown below in figure 4 is a confusion matrix of our win/loss 
      predictions using our sequential model with 3 dense layers. The test set encompasses the games from about the past two seasons, 
      2020-21 and 2021-22, with training on previous 8 seasons before that. We also have ran initial testing on just the 2022-23 season 
      alone, which has just been completed, and our results have been around 65%. This small dip in accuracy could mean the model is 
      overfitting to past seasons, and we may need a way to make recent seasons more relevant.
      </Typography>
      <br></br>
      <img src={neuralnet} alt="Confusion Matrix" style={{height:'300px'}}></img>
      <br></br>
      <Typography mr={15} ml={15}>
      We saw success when looking at spreads as well. Our neural network model had a success rate around 60% when predicting spreads 
      over the 2023 season and previous seasons. A bettor with a unit size of 10 dollars would have profited about 4000 dollars if they
      had bet on every game of the season, shown in the graph below.
      </Typography>
      <br></br>
      <img src={profit} alt="Profit Graph" style={{height:'300px'}}></img>
      <br></br>
      <Typography mr={15} ml={15}>
      This was just in classifying whether a team covers or not, our models struggled when trying to predict what the actual spread line itself
      should be. Just like before when predicting wins and losses we received an RMSE of around 11. Continued work needs to be done to try
      to lower this error and succesfully predict the lines. Even with the failure to predict lines themselves, our project was a huge success,
      still being able to turn a profit when betting spreads over an entire NBA season.
      </Typography>
      <br></br>
    </div>
  );
};
  
export default Results;